	module.exports = {"data":{"markdownRemark":{"html":"<p>Like JavaScript, Ruby and Python are high-level scripting languages that can be used for full stack web development.</p>\n<p>But even if you learned these languages, you’d still need to learn JavaScript. That’s because JavaScript is the only language that runs in web browsers. JavaScript has been around for 20 years, and it is still growing in popularity.</p>\n<p>Because of this, JavaScript has more tools and online learning resources than any other language.</p>\n<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/0/0fdd6f454cc04076bb5ed1334befbecc2b966913.png\" alt=\"A chart showing the volume of new GitHub repositories by year, with JavaScript growing and most languages declining.\"></p>","fields":{"slug":"/meta/why-does-free-code-camp-use-javascript-instead-of-ruby-or-python/"},"frontmatter":{"title":"Why Does Free Code Camp Use JavaScript Instead of Ruby or Python"}}},"pathContext":{"slug":"/meta/why-does-free-code-camp-use-javascript-instead-of-ruby-or-python/"}}

